import React from "react";
import { NavLink, Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react/index'
import { DatePicker, Select,  Form, Icon, Input, Badge, message, Menu, Dropdown, Modal } from 'antd';
import locale from 'antd/lib/date-picker/locale/zh_CN';
import moment from 'moment';
import { isAuthenticated } from "../../../utils/Session";
import api from "../../../api/api";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'
import 'moment/locale/zh-cn';
moment.locale('zh-cn');

const Option = Select.Option;
const { TextArea } = Input;

const getSvgIcon = (style = {}, text) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    const path1 = 'M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z'
    return (
        <i style={style}>
            <svg
                viewBox="0 0 1024 1024"
                width="1em"
                height="1em"
                fill="currentColor"
                style={{ verticalAlign: '-.125em' }}
            >
                <path d={path1} />
            </svg>
        </i>
    );
};
const MessageSvg = () => (
    <svg width="1em" height="1em" fill="currentColor" viewBox="0 0 1024 1024">
        <path d="M128 725.333333h170.666667v170.666667a42.624 42.624 0 0 0 72.832 30.165333L572.330667 725.333333H896a42.666667 42.666667 0 0 0 42.666667-42.666666V213.333333a42.666667 42.666667 0 0 0-42.666667-42.666666H128a42.666667 42.666667 0 0 0-42.666667 42.666666v469.333334a42.666667 42.666667 0 0 0 42.666667 42.666666zM170.666667 256h682.666666v384h-298.666666a42.496 42.496 0 0 0-30.165334 12.501333L384 793.002667V682.666667a42.666667 42.666667 0 0 0-42.666667-42.666667H170.666667V256z" fill="" p-id="1537"></path>
    </svg>
)
const MessageIcon = props => <Icon component={MessageSvg} {...props} />;

@inject('videoStore') @observer
class Index extends React.Component {
    constructor(props) {
        super(props)
        console.log(this.props)

    }
    state = {
        startValue: null,
        endTime: null,
        endOpen: false,
        backUserId: 1,
        deptList: [],
        stepSec: 1,
        deptId: '',
        pageNo: 1,
        videoList: [],
        total: 0,
        waitAuthCount: 0,
        selectId: '',
        examineSuccess: false,
        examineFail: false,
        examineNo: true,
        pages: 1,
        menu: (
            <Menu>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.alipay.com/">
                        1st menu item
                </a>
                </Menu.Item>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">
                        2nd menu item
                </a>
                </Menu.Item>
                <Menu.Item>
                    <a target="_blank" rel="noopener noreferrer" href="http://www.tmall.com/">
                        3rd menu item
                </a>
                </Menu.Item>
            </Menu>
        )
        , isShow: true,
        visibleStep: false
    }
    componentWillMount() {
        NProgress.start()
        console.log(isAuthenticated())
        console.log(moment().format('YYYY/MM/DD'))
        api('countDeptEmp', 'post').then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    deptList: res.data.data
                })
            }
        })
        let startTime, endTime
        if (!this.props.videoStore.startTimeVideo) {
            this.props.videoStore.setStartTimeVideo(moment('1970/01/01'))
            startTime = moment('1970/01/01')
        } else {
            startTime = this.props.videoStore.startTimeVideo
        }
        if (!this.props.videoStore.endTimeVideo) {
            this.props.videoStore.setEndTimeVideo(moment())
            endTime = moment()

        } else {
            endTime = this.props.videoStore.endTimeVideo

        }
        this.setState({
            startValue: startTime,
            endTime: endTime

        })
        console.log(isAuthenticated().split('?')[0])
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId
        })
        this.setState({

            deptId: userInfo.deptId
        })
        console.log(userInfo.deptId)

        console.log(this.props.videoStore.selectId)
        if (userInfo.backUserId == 1) {
            this.getList(userInfo.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', 1, userInfo.deptId).then(res => {
                console.log(res)
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        isShow: false

                    })
                } else {
                    this.setState({
                        isShow: true
                    })
                    message.warning(res.data.errorMessage, 2)
                }
            })
        } else {
            this.getList(userInfo.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', 1, userInfo.deptId).then(res => {
                console.log(res)
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        isShow: false

                    })
                } else {
                    this.setState({
                        isShow: true
                    })
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    componentDidMount() {
        NProgress.done()
        console.log(this.state.deptId)
    }
    componentWillUnmount() {
        NProgress.start()
    }
    getList = async (adminId, startTime, endTime, stepSec = 1, deptId = this.state.deptId, pageNo = 1, pageSize = 10, type = 2) => {
        let res = await api('getWaitAuthArticle', 'post', {}, {
            'deptId': deptId,
            'pageNo': pageNo,
            'pageSize': pageSize,
            'adminId': adminId,
            'startTime': startTime,
            'endTime': endTime,
            'stepFirst': stepSec,
            'type': type

        })
        return res
    }
    sendExamine = async (adminId, articleId, type = 2, rejectReason = '') => {
        let res = await api('deptAdminAuthArticle', 'post', {}, {
            'adminId': adminId,
            'articleId': articleId,
            'type': type,
            'rejectReason': rejectReason
        })


        return res
    }
    disabledStartDate = startValue => {
        const endTime = this.state.endTime;
        if (!startValue || !endTime) {
            return false;
        }
        return startValue.valueOf() > endTime.valueOf();
    }

    disabledEndDate = endTime => {
        const startValue = this.state.startValue;
        if (!endTime || !startValue) {
            return false;
        }
        return endTime.valueOf() <= startValue.valueOf();
    }

    onChange = (field, value) => {
        this.setState({
            [field]: value,
        });

    }

    onStartChange = value => {
        console.log(value)

        this.onChange('startValue', value);
        if (value) {
            this.props.videoStore.setStartTimeVideo(value)
            this.getList(this.state.backUserId, value ? moment(value).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, this.state.deptId).then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        pageNo: 1

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    };

    onEndChange = value => {
        console.log(value)

        this.onChange('endTime', value);
        if (value) {
            this.props.videoStore.setEndTimeVideo(value)

            this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', value ? moment(value).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, this.state.deptId).then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        pageNo: 1

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }



    handleEndOpenChange = open => {
        this.setState({ endOpen: open });
    }
    handleChange = (value) => {
        console.log(`selected ${value}`);
        this.props.videoStore.setSelectId(value)
        if (value == 'no') {
            this.setState({
                deptId: ''
            })
            this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, '').then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        pageNo: 1

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        } else {
            this.setState({
                deptId: value
            })
            this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, value).then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        waitAuthCount: res.data.waitAuthCount,
                        pages: res.data.pages,
                        pageNo: 1

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    itemRender = (current, type, originalElement) => {
        if (type === 'prev') {
            return <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />;
        }
        if (type === 'next') {
            return <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />;
        }

        return originalElement;
    }
    setexamineSuccess = () => {
        this.setState({
            examineSuccess: true,
            examineFail: false,
            examineNo: false,
            stepSec: 2
        })
        console.log(this.state.deptId)
        this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', 2, this.state.deptId).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    videoList: res.data.list,
                    total: res.data.total,
                    waitAuthCount: res.data.waitAuthCount,
                    pages: res.data.pages,
                    pageNo: 1

                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    setexamineFail = () => {
        this.setState({
            examineFail: true,
            examineSuccess: false,
            examineNo: false,
            stepSec: 3
        })
        console.log(this.state.deptId)
        console.log(this.props.videoStore.selectId)

        this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', 3, this.state.deptId).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    videoList: res.data.list,
                    total: res.data.total,
                    waitAuthCount: res.data.waitAuthCount,
                    pages: res.data.pages,
                    pageNo: 1

                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    setexamineNo = () => {
        this.setState({
            examineNo: true,
            examineSuccess: false,
            examineFail: false,
            stepSec: 1
        })
        this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', 1, this.state.deptId).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    videoList: res.data.list,
                    total: res.data.total,
                    waitAuthCount: res.data.waitAuthCount,
                    pages: res.data.pages,
                    pageNo: 1
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {

            this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, this.state.deptId.this.state.pageNo - 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo - 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    setNext = () => {
        if (this.state.pageNo == this.state.pages) {
            return
        } else {

            this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, this.state.deptId, this.state.pageNo + 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo + 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        videoList: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages
                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    setExamine = (val, id) => {
        let str = val == 2 ? 2 : 1
        this.setState({
            visibleStep: true,
            visibleStepId: id,
            visibleStepType: str,

        })

    }
    handleOkStep = () => {
        this.setState({
            visibleStep: true,
        })
    }
  handleCancelStep = () => {
        this.setState({
            visibleStep: false,
        })
    }
    handleSaveStep = () => {
        NProgress.start()
        let content=this.props.form.getFieldsValue(['reply']).reply
        this.sendExamine(this.state.backUserId, this.state.visibleStepId, this.state.visibleStepType,content).then(res => {
            NProgress.done()
            if (!res.data.errorMessage) {
                message.success('审核成功！')
                this.handleCancelStep()

                this.getList(this.state.backUserId, this.state.startValue ? moment(this.state.startValue).format('YYYY/MM/DD') : '1970/01/01', this.state.endTime ? moment(this.state.endTime).format('YYYY/MM/DD') + ' 23:59:59' : moment().formatformat('YYYY/MM/DD') + ' 23:59:59', this.state.stepSec, this.state.deptId, this.state.pageNo).then(res => {

                    if (!res.data.errorMessage) {
                        this.setState({
                            videoList: res.data.list,
                            waitAuthCount: res.data.total,
                            pages: res.data.pages,
                        })
                    } else {
                        message.warning(res.data.errorMessage, 2)
                    }
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }

        })
    }
    setMenu = (val, id) => {
        if (val == 1) {
            return (
                <Menu>
                    <Menu.Item>
                        <Link to={{ 'pathname': '/Video/Edit', params: { 'id': id } }}>
                            编辑
                    </Link>

                    </Menu.Item>


                    <Menu.Item>
                        <a target="_blank" rel="noopener noreferrer" href="javascript:;" onClick={() => { this.setExamine(1, id) }}>
                            通过
                    </a>
                    </Menu.Item>  <Menu.Item>
                        <a target="_blank" rel="noopener noreferrer" href="javascript:;" onClick={() => { this.setExamine(2, id) }}>
                            驳回稿件
                    </a>
                    </Menu.Item>


                </Menu>
            )
        } else {
            return (
                <Menu>
                    <Menu.Item>
                        <Link to={{ 'pathname': '/Video/Edit', params: { 'id': id } }}>
                            编辑
                        </Link>
                    </Menu.Item>

                    <Menu.Item>
                        <Link to='/Video/Index/VideoPage'>
                            上架管理
                        </Link>
                    </Menu.Item>
                </Menu>
            )
        }

    }
    getVideo = () => {
        // if(this.state.stepType=='all'){

        return this.state.videoList && this.state.videoList.map(res => {
            return (
                <div className="list" key={res.id}>
                    <div className="flexBox">
                        <div className="flex_l">
                            <img src={res.firstImg} alt="" />
                        </div>
                        <div className="flex">
                            <div className="title">{res.title}</div>
                            <div className="flexBox list_foot">
                                <div className="look">
                                    <Icon type="eye" style={{ color: '#999999', fontSize: '16px' }} />
                                    浏览 {res.browseTimes ? res.browseTimes : 0}
                                </div>
                                <div className="star">
                                    <Icon type="like" style={{ color: '#999999', fontSize: '16px' }} />
                                    点赞 {res.starCount ? res.starCount : 0}
                                </div>
                                <div className="comment">
                                    <MessageIcon style={{ color: '#999999', fontSize: '16px' }} />
                                    评论 {res.replyCount ? res.replyCount : 0}
                                </div>
                            </div>
                        </div>
                    </div>
                    {
                        res.stepSec == 2 ?
                            <img className='topImg' src={require('../../../assets/images/img-article-success.png')} alt="" /> : ''
                    }
                    {
                        res.stepSec == 3 ?
                            <img className='topImg' src={require('../../../assets/images/img-stepNo.png')} alt="" /> : ''
                    }
                    {
                        res.stepSec == 1 && res.stepFirst == 2 ?
                            <img className='topImg' src={require('../../../assets/images/img-columnStep.png')} alt="" /> : ''
                    }
                    {
                        res.stepFirst == 1 ?
                            <img className='topImg' src={require('../../../assets/images/img-deptColumn.png')} alt="" /> : ''
                    }
                    <Dropdown overlay={this.setMenu(this.state.backUserId != 1 ? res.stepFirst : res.stepSec, res.id)} placement="bottomCenter">
                        <div className="operation">
                            操作
                     </div>
                    </Dropdown>
                </div>
            )
        })

    }
    render() {
        const { startValue, endTime, endOpen } = this.state;
        const { getFieldDecorator } = this.props.form;

        return (
            <div id={this.state.isShow ? 'hide' : 'examineVideo'}>
                <div className="head">
                    <div className="timeBox">
                        <div className="label">时间：</div>
                        <div className='time'>
                            <DatePicker
                                disabledDate={this.disabledStartDate}
                                format="YYYY年MM月DD日"
                                value={startValue}
                                onChange={this.onStartChange}
                                locale={locale}
                                suffixIcon={getSvgIcon()}

                            />
                            至
                        <DatePicker
                                disabledDate={this.disabledEndDate}
                                format="YYYY年MM月DD日"
                                onChange={this.onEndChange}
                                value={endTime}
                                locale={locale}
                                suffixIcon={getSvgIcon()}
                            />
                        </div>
                    </div>
                    <div className="deptBox">
                        <div className="label">部门：</div>

                        <div className="dept">
                            {

                                <Select defaultValue={this.state.deptId ? this.state.deptId : 'no'} style={{ width: 183 }} onChange={this.handleChange}>
                                    {
                                        this.state.deptList.length > 0 && this.state.deptList.map(res => {
                                            if (this.state.deptId == res.deptId) {
                                                return (
                                                    <Option key={res.deptId} value={res.deptId}>{res.deptName}</Option>
                                                )
                                            }

                                        })
                                    }

                                </Select>
                            }
                        </div>
                    </div>
                </div>
                <Link to='/Video/Edit' className="adit">
                    新建文章
                </Link>
                <div className="checkType">
                    <NavLink to='/Video/Index' className="allType" exact activeClassName='link-active'>
                        全部文章
                    </NavLink>
                    <NavLink to='/Video/Index/ExamineDept' onClick={this.setexamineNo} className={this.state.examineNo ? 'noExamineType link-active' : 'noExamineType'} activeClassName={this.state.examineSuccess || this.state.examineFail ? '' : 'link-active'}>
                        <Badge count={this.state.waitAuthCount} overflowCount={99} offset={[6, -6]}>

                            未审核
                    </Badge>
                    </NavLink>
                    <span className={this.state.examineSuccess ? 'examineSuccess link-active' : 'examineSuccess'} onClick={this.setexamineSuccess}>
                        审核通过
                    </span>
                    <span className={this.state.examineFail ? 'examineFail link-active' : 'examineFail'} onClick={this.setexamineFail}>
                        审核失败
                    </span>
                </div>
                <div className="videoList">
                    {
                        this.getVideo()
                    }
                    {
                        this.state.videoList.length == 0 ? <div className='noData'>
                            <img src={require('../../../assets/images/img-nodata.png')} alt="" />
                            <p>暂无相关文章</p>
                        </div> : ''
                    }
                    {
                        this.state.pages <= 1 ? '' : <div className='pagination'>
                            <span onClick={this.setPrev}>
                                <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                            <span>
                                {this.state.pageNo}/{this.state.pages}
                            </span>
                            <span onClick={this.setNext}>
                                <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                        </div>
                    }
                </div>
                <Modal
                    title="审核理由"
                    visible={this.state.visibleStep}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOkStep}
                    onCancel={this.handleCancelStep}
                    width='52%'
                    wrapClassName='setStep'
                >
                    <div className='replyInfo'>
                        <Form>
                            <Form.Item>
                                {getFieldDecorator('reply', {
                                    rules: [{ required: false, }],
                                })(
                                    <TextArea
                                        placeholder="输入理由"
                                        autosize={{ minRows: 6 }}
                                    />
                                )}
                            </Form.Item>

                        </Form>
                    </div>
                    <div className="btn">
                        <span className='active' onClick={this.handleSaveStep}>确认</span>
                        <span onClick={this.handleCancelStep}>取消</span>
                    </div>
                </Modal>
            </div>
        )
    }
}
const WrappedApp = Form.create({ name: 'coordinated' })(Index);
export default WrappedApp
